const tl = new TimelineMax();
const title = document.querySelector('.title');

const colAside = document.querySelector('.col-aside');
const colMain = document.querySelector('.col-main');

const DURATION = 0.5;

tl.fromTo(title, { x: -200, opacity: 0,}, {x: 0, duration: DURATION, opacity: 1,})
  .fromTo(colAside, {x: -200, opacity: 0,}, {x: 0, duration: DURATION, opacity: 1,})
  .fromTo(colMain, {x: 200, opacity: 0,}, {x: 0, duration: DURATION, opacity: 1,});


